<template>
    <div class="main">
        <iframe
            v-loading="show"
            :src="src"
            title="Interactive or visual content"
            frameborder="0"
            scrolling="no"
            ref="iframe"
            style="width:70%;height:700px"
            sandbox="allow-same-origin allow-forms allow-scripts allow-downloads allow-popups allow-popups-to-escape-sandbox allow-top-navigation-by-user-activation"
            @load="show = true"
        ></iframe>
        <div style="width:100%!;margin-top:4px!important;text-align:right!important;">
            <a
                class="flourish-credit"
                href="https://public.flourish.studio/visualisation/5695286/?utm_source=embed&utm_campaign=visualisation/5695286"
                target="_top"
                style="text-decoration:none!important"
            >
                <img
                    alt="Made with Flourish"
                    src="https://public.flourish.studio/resources/made_with_flourish.svg"
                    style="width:105px!important;height:16px!important;border:none!important;margin:0!important;"
                />
            </a>
        </div>

        <el-switch style="display: block" v-model="value" active-color="#67C23A" inactive-color="#409eff" inactive-text="标准差标准化" active-text="最大最小值标准化"> </el-switch>
    </div>
</template>

<script>
    import { animate } from "@/function/animate.js";
    export default {
        data() {
            return {
                src: "https://flo.uri.sh/visualisation/5634159/embed",
                value: false,
                show: false,
            };
        },
        watch: {
            value(val) {
                let src = `https://flo.uri.sh/visualisation/${val ? 5722230 : 5634159}/embed`;
                if (this.src != src) this.src = src;
                animate(this.$refs.iframe, "fadeInUp");
            },
        },
    };
</script>
<style lang="less">
    .el-switch {
        position: absolute;
        transform: rotate(90deg);
        right: 10px;
        top: 50%;
    }
</style>
